/* Contenedor general */
.mapaContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Header */
.mapaHeader {
  background-color: #4b6cb7;
  color: #fff;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mapaHeader h1 {
  margin: 0;
  font-size: 1.8rem;
}

.dashboardButton {
  background-color: #fff;
  color: #4b6cb7;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.dashboardButton:hover {
  background-color: #e0e0e0;
}

/* Contenido */
.mapaContent {
  display: flex;
  flex: 1;
}

/* Mapa */
.mapa {
  height: 100%;
}

/* Sidebar */
.sidebar {
  width: 30%;
  background: #f5f5f5;
  border-left: 1px solid #ddd;
  padding: 1rem;
  overflow-y: auto;
  box-shadow: -3px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar h2 {
  margin-top: 0;
  color: #333;
}

.sidebar h3 {
  margin-bottom: 1rem;
  color: #555;
}

.candidatoItem {
  margin-bottom: 1rem;
  padding: 0.5rem;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.05);
}

.candidatoItem p {
  margin: 0.2rem 0;
  color: #666;
}

/* Bot√≥n cerrar */
.cerrarButton {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background: #4b6cb7;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

.cerrarButton:hover {
  background-color: #3a539b;
}

